<template>
    <div class="edit-part-wrapper" v-if="part">
      <div class="edit-part">
        <div class="image-container">
          <img :src="part.filePath" alt="Part Image" class="part-image" />
        </div>
        <div class="form-container">
          <h3>Edit Part</h3>
          <form @submit.prevent="savePart">
            <div class="form-group">
              <label for="name">Part Name</label>
              <input type="text" v-model="part.name" placeholder="Enter part name" required />
            </div>
  
            <div class="form-group">
              <label for="type">Type (e.g., head, eyes, etc.)</label>
              <input type="text" v-model="part.type" placeholder="Enter part type" required />
            </div>
  
            <div class="form-group">
              <label for="filePath">File Path</label>
              <input type="text" v-model="part.filePath" placeholder="Enter file path" required />
            </div>
  
            <div class="form-group">
              <label for="tags">Tags (comma-separated)</label>
              <input type="text" v-model="tagsInput" placeholder="Enter tags" />
            </div>
  
            <button type="submit">Save Part</button>
            <button type="button" @click="confirmDelete" class="delete-button">Delete Part</button>
          </form>
        </div>
      </div>
  
      <!-- Confirmation Popup for Deletion -->
      <div v-if="showDeletePopup" class="delete-popup-overlay">
        <div class="delete-popup">
          <p>Are you sure you want to delete this part?</p>
          <div class="popup-buttons">
            <button @click="deletePart">Yes</button>
            <button @click="closeDeletePopup">No</button>
          </div>
        </div>
      </div>
    </div>
  
    <div v-else>
      <p>Loading part details...</p>
    </div>
  </template>
  
  <script>
  import { onValue, ref, remove, update } from "firebase/database";
import { database } from "../firebaseConfig";
  
  export default {
    data() {
      return {
        part: null,
        tagsInput: '',
        showDeletePopup: false,
      };
    },
    methods: {
      fetchPart(name) {
        const partsRef = ref(database, 'parts');
        onValue(partsRef, (snapshot) => {
          const partsData = snapshot.val();
          const part = Object.values(partsData).find(p => p.name === name);
          if (part) {
            this.part = part;
            this.tagsInput = this.part.tags.join(', ');
          } else {
            console.error("Part not found");
          }
        });
      },
      savePart() {
        if (this.part) {
          this.part.tags = this.tagsInput.split(',').map(tag => tag.trim());
          const partRef = ref(database, `parts/${this.part.id}`);
          update(partRef, this.part)
            .then(() => alert('Part updated successfully'))
            .catch(error => console.error('Error updating part:', error));
        }
      },
      confirmDelete() {
        this.showDeletePopup = true;
      },
      closeDeletePopup() {
        this.showDeletePopup = false;
      },
      deletePart() {
        const partRef = ref(database, `parts/${this.part.id}`);
        remove(partRef)
          .then(() => {
            alert('Part deleted successfully');
            this.$router.push('/find-parts');
          })
          .catch(error => console.error('Error deleting part:', error));
      }
    },
    mounted() {
      const partName = this.$route.params.name;
      if (partName) {
        this.fetchPart(partName);
      } else {
        console.error('Part name is missing from route params');
      }
    }
  };
  </script>
  
