<template>
  <div class="find-parts-wrapper">
    <div class="find-parts-container">
      <input
        type="text"
        v-model="searchQuery"
        placeholder="Search parts by name..."
        class="search-bar"
        @input="searchParts"
      />

      <!-- Display results in a grid -->
      <div class="masonry-grid">
        <div v-for="(part, index) in filteredParts" :key="index" class="card">
          <div class="card-content">
            <img :src="part.filePath" alt="Part Image" class="part-image" />
            <div class="card-footer">
              <p>{{ part.name }}</p>
              <button @click="openEditPage(part)">Info</button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onValue } from "firebase/database";
import { database } from "../firebaseConfig";

export default {
  data() {
    return {
      searchQuery: '',
      parts: [],
      filteredParts: []
    };
  },
  methods: {
    fetchParts() {
      const partsRef = ref(database, 'parts');
      onValue(partsRef, (snapshot) => {
        const partsData = snapshot.val();
        this.parts = partsData ? Object.values(partsData) : [];
        this.filteredParts = []; // Clear the grid until a search is made
      });
    },
    searchParts() {
      if (this.searchQuery.trim() === '') {
        this.filteredParts = [];
      } else {
        this.filteredParts = this.parts.filter(part =>
          part.name.toLowerCase().includes(this.searchQuery.toLowerCase())
        );
      }
    },
    openEditPage(part) {
      if (part && part.name) {
        this.$router.push({ name: 'EditPart', params: { name: part.name } });
      } else {
        console.error('Part name is missing');
      }
    }
  },
  mounted() {
    this.fetchParts();  // Fetch parts when the component is mounted
  }
};
</script>

