<template>
  <div class="add-part-wrapper">
    <div class="add-part">
      <button @click="$router.push('/')">Back</button>
      <h3>Add a New Part</h3>
      <form @submit.prevent="addPart">
        <div class="form-group">
          <label for="name">Part Name</label>
          <input type="text" v-model="part.name" placeholder="Enter part name" required />
        </div>
        
        <div class="form-group">
          <label for="type">Type (e.g., head, eyes, etc.)</label>
          <input type="text" v-model="part.type" placeholder="Enter part type" required />
        </div>
        
        <div class="form-group">
          <label for="filePath">File Path</label>
          <input type="text" v-model="part.filePath" placeholder="Enter file path" required />
        </div>

        <div class="form-group">
          <label for="tags">Tags (comma-separated)</label>
          <input type="text" v-model="tagsInput" placeholder="Enter tags" />
        </div>

        <button type="submit">Add Part</button>
      </form>
    </div>
  </div>
</template>

<script>
import { push, ref } from "firebase/database";
import { database } from '../firebaseConfig'; // Ensure correct import path

export default {
  data() {
    return {
      part: {
        name: '',
        type: '',
        filePath: '',
        tags: [],
      },
      tagsInput: ''
    };
  },
  methods: {
    addPart() {
      // Split tags from the input string
      this.part.tags = this.tagsInput.split(',').map(tag => tag.trim());
      
      // Save part to Firebase
      const partsRef = ref(database, 'parts');
      push(partsRef, this.part).then(() => {
        // Reset form after submission
        this.part = { name: '', type: '', filePath: '', tags: [] };
        this.tagsInput = '';
      });
    }
  }
};
</script>

