<template>
  <div :class="['sliding-menu', { 'is-open': isOpen }]">
    <button class="close-button" @click="closeMenu">X</button>
    <h3>Select a {{ part }}</h3>
    
    <!-- None button -->
    <button @click="selectNone" class="none-button">None</button>

    <div v-if="thumbnails.length > 0" class="thumbnail-grid">
      <div
        v-for="option in thumbnails"
        :key="option.id"
        class="thumbnail-container"
        @click="selectThumbnail(option)"
      >
        <img :src="option.filePath" :alt="option.name" class="thumbnail" />
        <p>{{ option.name }}</p>
      </div>
    </div>
    <p v-else>No images found for this part type.</p>
  </div>
</template>

<script>
import { onValue, ref } from "firebase/database";
import { database } from "../firebaseConfig";

export default {
  props: ['isOpen', 'part'],
  data() {
    return {
      thumbnails: [],  // Will hold the fetched thumbnails
    };
  },
  methods: {
    closeMenu() {
      this.$emit('close');
    },
    selectThumbnail(option) {
      this.$emit('part-selected', option.filePath);  // Emit selected part to the parent
    },
    selectNone() {
      this.$emit('part-selected', 'none');  // Emit 'none' to indicate removal
    },
    fetchThumbnails() {
      const partsRef = ref(database, 'parts');  // Path to parts in Firebase
      onValue(partsRef, (snapshot) => {
        const data = snapshot.val();

        if (data) {
          // Filter parts based on the selected part type (e.g., head, nose)
          this.thumbnails = Object.values(data).filter(part => part.type === this.part);
        } else {
          console.warn(`No parts found for type: ${this.part}`);
        }
      }, (error) => {
        console.error("Error fetching parts:", error);
      });
    }
  },
  watch: {
    part(newPart) {
      if (newPart) {
        this.fetchThumbnails();  // Fetch thumbnails whenever the part changes
      }
    }
  },
  mounted() {
    this.fetchThumbnails();  // Initial fetch when component is mounted
  }
};
</script>

