  position: fixed;
  top: 0;
  right: -300px;
  width: 300px;
  height: 100vh;
  background-color: #333;
  color: white;
  z-index: 1000;
  transition: right 0.5s ease;
  padding: 20px;
}

.sliding-menu.is-open {
  right: 0;
}

.close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: 1px solid white;
  color: white;
  font-size: 20px;
  cursor: pointer;
}

.thumbnail-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  grid-gap: 10px;
}

.thumbnail-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #f9f9f9;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.thumbnail {
  width: 100px;
  height: 100px;
  cursor: pointer;
  object-fit: contain;  /* Ensure full coverage without overflow */
}

.thumbnail:hover {
  transform: scale(1.1);
  transition: transform 0.2s ease;
}

.none-button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  background-color: #ff5555;
  color: white;
  border: none;
  cursor: pointer;
}

.none-button:hover {
  background-color: #ff7777;
}
</style>
